import initialState from './initialState'
import { Monday } from '../Components/Monday';
//import uuid from 'uuid';


const MainReducer = (state = initialState, action) => {
    switch (action.type) {

    case 'changeAssignment':
        console.log(state)
        const user = action.data.user;
        const day = action.data.day;
        const time = action.data.time;
        const interval = action.data.interval;
        //const change = state[user][day][time][interval];
        const isAssigned = action.data.assigned;
        const location = action.data.location;
        //console.log(change.assigned)
        //console.log(state[user][day][time][interval]);
        const user1 = {
           // [user] : {
                ...state[user],
                [day]:{
                     ...state[user][day],
                     [time]:{
                        ...state[user][day][time],
                         [interval]:{
                            ...state[user][day][time][interval],
                            assigned: isAssigned,
                            location: location,
                         }
                     }
                     
                }
           // }
        }
       
        return {  ...state,[user]:user1}
    default:
        return state
    }
}


export default MainReducer;